<template>
    <div>
        <!---->
        <el-row style="margin-bottom: 22px;">
            <div class="fromtitle">
                <i class="titicon"></i>
                <span class="tittxt"></span>
            </div>
            <el-table :data="tableData" border style="width: 90%;margin-top: 16px;">
                <el-table-column label="" align="center" width="60">
                    <template slot-scope="scope">
                        <span>{{scope.$index + 1}}</span>
                    </template>
                </el-table-column>
                <el-table-column label="" width="180">
                    <template slot-scope="scope">
                        <span>{{scope.row.name}}</span>
                    </template>
                </el-table-column>
                <el-table-column label="" width="220">
                    <template slot-scope="scope">
                        <span>{{scope.row.description}}</span>
                    </template>
                </el-table-column>
                <el-table-column label=" ">
                    <template slot-scope="scope">
                        <span>{{scope.row.syllabus_id}} - {{scope.row.syllabus_name}}</span>
                    </template>
                </el-table-column>
                <el-table-column label="" align="center" width="160">
                    <template slot-scope="scope">
                        <el-button size="small" type="text" @click="handleDelete(scope.$index, scope.row)">
                        </el-button>
                        <el-button size="small" type="text" @click="handleEdit(scope.$index, scope.row)"></el-button>
                        <el-button size="small" type="text" @click="handleCheck(scope.row)"></el-button>
                    </template>
                </el-table-column>
            </el-table>
            <div class="addBox">
                <div @click="addTableData" style="cursor: pointer;display: inline-block;padding: 0 30px;">
                    <i class="additem"></i>
                    <el-button type="text" class="addBtn"></el-button>
                </div>
            </div>

        </el-row>

        <el-dialog class="addContent tabplane" :title="Doing == 'addDate'?'':''" :visible.sync="dialogVisible" @close="cancel('NewTableForm')">

            <el-form :model="NewTableForm" ref="NewTableForm" label-width="100px">
                <el-form-item label="" prop="name" :rules="filter_rules({required:true,type:'isAllSpace',max:10})">
                    <el-input v-model="NewTableForm.name" class="coursetxt"></el-input>
                </el-form-item>
                <el-form-item label="" prop="description" :rules="filter_rules({required:true,type:'isAllSpace',max:20})">
                    <el-input v-model="NewTableForm.description" class="coursetxt"></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-radio v-model="chooseOutlineRadio" label="1"></el-radio>
                    <el-radio v-model="chooseOutlineRadio" label="2"></el-radio>
                </el-form-item>
                <el-form-item v-if="chooseOutlineRadio == 2" label="" prop="syllabus_id" :rules="[{required: true, message: '', trigger: 'change'}]">
                    <el-select @change="handleChange" v-model="NewTableForm.syllabus_id" placeholder="" style="width: 90%;">
                        <el-option :label="item.id+' - '+item.title" :value="String(item.id)" v-for="item in outlineList" :key="item.id"></el-option>
                    </el-select>
                    <el-button type="primary" class="checkOutlineBtn" @click="openSyllabusPage"></el-button>
                </el-form-item>
                <el-form-item v-if="chooseOutlineRadio == 1" label="" prop="title" :rules="filter_rules({required:true,type:'isAllSpace',max:50})">
                    <!-- <el-input class="coursetxt" v-model="title"></el-input> -->
                    <el-input class="coursetxt" v-model="NewTableForm.title" placeholder="50"></el-input>
                </el-form-item>

                <el-form-item class="coursebtn">
                    <el-button @click="cancel('NewTableForm')"> </el-button>
                    <el-button type="primary" @click="updateTable('NewTableForm')" v-if="Doing=='update'"> 
                    </el-button>
                    <el-button type="primary" @click="addTable('NewTableForm')" v-if="Doing=='addDate'"> </el-button>
                </el-form-item>
            </el-form>

        </el-dialog>

    </div>
</template>
<style lang="less">
.checkOutlineBtn {
    margin-top: 20px;
}
</style>

<script>
export default {
    components: {},
    data() {
        return {
            ruleForm: {
                bEnabled: "1"
            },
            NewTableForm: {},
            dialogVisible: false,
            currentTemplate: "", //  
            tableData: [],
            operation: "",
            Doing: "",
            gradation_id: "", //id 
            chooseOutlineRadio: "2" //
        };
    },
    methods: {
        // 
        addTable(formName) {
            this.$refs[formName].validate(valid => {
                if (valid) {
                    this.AddCourseStage();
                } else {
                    console.log("error submit!!");
                    return false;
                }
            });
        },
        handleChange(sy_id) {
            this.outlineList.forEach(item => {
                if (item.id == sy_id) {
                    this.currentTemplate = item.template;
                }
            });
        },
        //
        openSyllabusPage() {
            let sy_id = this.NewTableForm.syllabus_id;
            if (sy_id) {
                if (this.currentTemplate) {
                    window.open(`/#/syllabus/manage/edit/${sy_id}`);
                } else {
                    window.open(`/#/syllabus/manage/template/${sy_id}`);
                }
            } else {
                window.open("/#/syllabus/manage/list");
            }
        },
        // 
        handleCheck(row) {
            let sy_id = row.syllabus_id;
            let currentTemplate;
            this.outlineList.forEach(item => {
                if (item.id == sy_id) {
                    currentTemplate = item.template;
                }
            });
            if (sy_id) {
                if (currentTemplate) {
                    window.open(`/#/syllabus/manage/edit/${sy_id}`);
                } else {
                    window.open(`/#/syllabus/manage/template/${sy_id}`);
                }
            } else {
                window.open("/#/syllabus/manage/list");
            }
        },
        // 
        async AddCourseStage() {
            let stageParams = {};
            if (this.chooseOutlineRadio == "1") {
                //
                let syllabus_id = await this.createSyllabus();
                if (!syllabus_id) {
                    this.$message.error("");
                    return;
                } else {
                    stageParams = {
                        name: this.NewTableForm.name, //
                        syllabus_id: syllabus_id, //id
                        description: this.NewTableForm.description, //
                        course_id: this.course_id //id
                    };
                }
            } else if (this.chooseOutlineRadio == "2") {
                // 
                stageParams = {
                    name: this.NewTableForm.name, //
                    syllabus_id: this.NewTableForm.syllabus_id, //id
                    description: this.NewTableForm.description, //
                    course_id: this.course_id //id
                };
            } else {
                return;
            }
            let ret = await this.$http.AddCourseStage(stageParams);
            if (ret.status == 0) {
                this.$message({
                    type: "success",
                    message: ret.message
                });
                this.dialogVisible = false;
                this.getStageAndOutline();
            } else if (ret.status > 0) {
                ret.message ? ret.message : "";
                this.$message.error(ret.message);
            }
        },
        //
        async createSyllabus() {
            let stageParams = {};
            //
            let syllabusParams = {
                title: this.NewTableForm.title,
                project_id: this.project_id, //id
                subject_id: this.subject_id, //id
                status: "0" // 0- 1-
            };
            let syllabusRet = await this.$http.CourseSyllabus(syllabusParams);
            if (syllabusRet.status == 0) {
                let outlineList = JSON.parse(JSON.stringify(this.outlineList));
                if(!(outlineList instanceof Array)){
                    outlineList = [];
                }
                outlineList.unshift(syllabusRet.result);
                this.$store.dispatch('saveCourseSyllabuses', outlineList);   //
                let syllabus_id = syllabusRet.result.id; //id
                return syllabus_id;
            } else {
                return;
            }
        },
        //
        updateTable(formName) {
            this.$refs[formName].validate(valid => {
                if (valid) {
                    this.changeStage();
                } else {
                    return false;
                }
            });
        },
        //
        async changeStage() {
            let gradation_id = this.gradation_id;
            let stageParams = {};
            if (this.chooseOutlineRadio == "1") {
                //
                let syllabus_id = await this.createSyllabus();
                if (!syllabus_id) {
                    this.$message.error("!");
                    return;
                } else {
                    stageParams = {
                        name: this.NewTableForm.name, //
                        syllabus_id: syllabus_id, //id
                        description: this.NewTableForm.description, //
                        course_id: this.course_id //id
                    };
                }
            } else if (this.chooseOutlineRadio == "2") {
                // 
                stageParams = {
                    name: this.NewTableForm.name, //
                    syllabus_id: this.NewTableForm.syllabus_id, //id
                    description: this.NewTableForm.description, //
                    course_id: this.course_id //id
                };
            } else {
                return;
            }
            let ret = await this.$http.changeStage(gradation_id, stageParams);
            if (ret.status == 0) {
                this.$message({
                    type: "success",
                    message: ret.message
                });
                this.dialogVisible = false;
                this.getStageAndOutline();
            } else if (ret.status > 0) {
                ret.message ? ret.message : "!";
                this.$message.error(ret.message);
            }
        },
        handleDelete(index, row) {
            this.gradation_id = row.id;
            this.$confirm(", ?", "", {
                confirmButtonText: "",
                cancelButtonText: "",
                type: "warning"
            })
                .then(() => {
                    this.DeleteStage();
                })
                .catch(() => {
                    this.$message({
                        type: "info",
                        message: ""
                    });
                });
        },
        //
        async DeleteStage() {
            let gradation_id = this.gradation_id;
            let ret = await this.$http.DeleteStage(gradation_id);
            if (ret.status == 0) {
                this.$message({
                    type: "success",
                    message: "!"
                });
                this.getStageAndOutline();
            } else if (ret.status > 0) {
                ret.message ? ret.message : "";
                this.$message.error(ret.message);
            }
        },
        //
        addTableData() {
            this.NewTableForm = {
                name: "",
                description: "",
                syllabus_id: "",
                title: ""
            };
            this.chooseOutlineRadio = "2";
            this.Doing = "addDate";
            this.dialogVisible = true;
        },
        //
        handleEdit(index, row) {
            this.Doing = "update";
            this.gradation_id = row.id;
            let sy_id = row.syllabus_id;
            this.chooseOutlineRadio = "2";
            this.NewTableForm = { ...this.tableData[index] };
            this.dialogVisible = true;
            this.handleChange(sy_id);
        },
        cancel(formName) {
            this.$refs[formName].resetFields();
            this.dialogVisible = false;
        },
        async getStageAndOutline() {
            let course_id = this.course_id;
            let ret = await this.$http.getStageAndOutline(course_id);
            if (ret.status == 0) {
                this.tableData = ret.result.gradation_list;
            }
        }
    },
    computed: {
        course_id() {
            return this.$route.params.cid;
        },
        outlineList() {
            return this.$store.state.course.course_Syllabuses;
        },
        project_id() {
            if (this.$store.state.course.course_info) {
                return this.$store.state.course.course_info.project_id;
            }
        },
        subject_id() {
            if (this.$store.state.course.course_info) {
                return this.$store.state.course.course_info.subject_id;
            }
        }
    },
    mounted() {},
    created() {
        this.getStageAndOutline();
    }
};
</script>



